<template>
  <div>

    <div class="card">
      
      <div class="card-header text-start" style="font-family: 'Andika New Basic';">
        <span style="padding-top:8px"> <i class="fas fa-user-circle fa-fw fa-2x"></i> </span>
        <span style="margin-bottom: 0; font-size: 1.6rem"> {{ usernameFull }} </span>
        <p class="text-muted" style="margin-left: 47px; font-size: 95%; margin-top: -2px"> {{ postCreationTimeAgo }} </p>
      </div>
      
      <div class="card-body text-start">
        <h5 class="card-title"> </h5>
        <p class="card-text" style="font-family: 'Kite one';"> {{ post.caption }} </p>
        <img :src="post.image" :alt="post.author.username" class="img-thumbnail" srcset="" style="width: 100%">
      </div>
      
      <div class="card-footer text-start text-muted">
        <div class="float-start">
          <span class="post-react" @click="reactToPost()" style="cursor: pointer">
            <i class="far fa-heart fa-fw fa-2x" v-if="!isUserReactToPost" ></i>
            <i class="fas fa-heart fa-fw fa-2x" v-if="isUserReactToPost" style="color: #c30a0a;"></i>
          </span>
          <span class="post-comment" style="margin-left: 10px; cursor: pointer">
            <i class="far fa-comment fa-fw fa-2x"></i>
          </span>
        </div>
        <div class="float-end" style="font-family: 'Langar';"> {{ post.reacts.length }} react . {{ post.comments.length }} comment </div>
      </div>
    </div>

  </div>
</template>

<script>
  import utils from '@/utils.js';
  import NProgress from 'nprogress';
  import EventService from '@/services/EventService.js';

  export default {
    name: 'Post-Card',

    props: {
      post: Object,
      user: Object,
      index: Number,
    },

    data (){
      return {
        isLoggedIn: false,
        isReactTrigger: false,
      }
    },

    methods: {
      async reactToPost (){
        NProgress.start();
        await this.checkLoginStatus();

        if (this.isLoggedIn){
          if (this.isUserReactToPost) {  // remove react ------------------------------
            EventService.deleteReact(this.post._id, this.post.reacts[this.reactIndex]._id)
              .then (res => {
                if (res.data[1].catagory.toLowerCase() === 'success'){
                  NProgress.done();
                  this.isReactTrigger = false;
                  this.$store.dispatch('postStore/deletePostsReact', {postIndex: this.index, reactIndex: this.reactIndex});
                } else {
                  NProgress.done();
                  this.$store.dispatch('notificationStore/showNotification', res.data[1]);
                }
              })
              .catch (err => {
                this.$store.dispatch('notificationStore/showNotification', {catagory: 'error', title: 'Connection Problem', message: err });
                NProgress.done();
              })
          } else {  // add react -----------------------------------------------------
            EventService.createReact(this.post._id, {reaction: true})
              .then (res => {
                if (res.data[1].catagory.toLowerCase() === 'success'){
                  NProgress.done();
                  this.isReactTrigger = true;
                  this.$store.dispatch('postStore/updatePosts', {post: res.data[0], index: this.index});
                } else {
                  NProgress.done();
                  this.$store.dispatch('notificationStore/showNotification', res.data[1]);
                }
              })
              .catch (err => {
                this.$store.dispatch('notificationStore/showNotification', {catagory: 'error', title: 'Check this error', message: err.message });
                NProgress.done();
              })
          }
        } else {
          this.$store.dispatch('notificationStore/showNotification', {catagory: 'error', title: 'Log in required', message: `To react this post you need to be logged in` });
        }
      },

      checkLoginStatus (){
        NProgress.start()
        return EventService.isLoggedIn()
          .then (res => {
            NProgress.done();
            this.isLoggedIn = res.data[1];
            this.$store.dispatch('postStore/saveUser', res.data[0]);
          })
          .catch (err => {
            this.$store.dispatch('notificationStore/showNotification', {catagory: 'error', title: 'Connection Problem', message: `You are offline.` });
            NProgress.done();
          })
      },
    },

    computed: {
      usernameFull: function (){
        let username = this.post.author.username;
        return username.split(' ').map(item => item.slice(0,1).toUpperCase() + item.slice(1).toLowerCase()).join(' ')
      },

      isUserReactToPost: function(){
        if (!this.user) return false;
        if (this.isReactTrigger) return this.isReactTrigger;
        return this.post.reacts.filter(item => item.author === this.user._id).length
      },

      reactIndex: function (){
        if (!this.user) return undefined;
        let arr = this.post.reacts.map(item => item.author === this.user._id ? true : false)
        return arr.indexOf(true)
      },

      postCreationTimeAgo: function (){
        let presentDate = new Date(),
            postDate    = new Date(this.post.date);

        return (presentDate.getFullYear() > postDate.getFullYear()) ? `${presentDate.getFullYear() - postDate.getFullYear()} year ago` :
          (presentDate.getMonth() > postDate.getMonth()) ? `${presentDate.getMonth() - postDate.getMonth()} month ago` :
            (presentDate.getDate() > postDate.getDate()) ? `${presentDate.getDate() - postDate.getDate()} day ago` :
              (presentDate.getHours() > postDate.getHours()) ? `${presentDate.getHours() - postDate.getHours()} hour ago` :
                `${presentDate.getMinutes() - postDate.getMinutes()} minutes ago`;

        /* if(presentDate.getFullYear() > postDate.getFullYear()){
          return `${presentDate.getFullYear() - postDate.getFullYear()} year(s) ago`;
        }
        if (presentDate.getMonth() > postDate.getMonth()){
          return `${presentDate.getMonth() - postDate.getMonth()} month(s) ago`;
        }
        if (presentDate.getDate() > postDate.getDate()){
          return `${presentDate.getDate() - postDate.getDate()} day(s) ago`;
        }
        if (presentDate.getHours() > postDate.getHours()){
          return `${presentDate.getHours() - postDate.getHours()} hour(s) ago`;
        }
        return `${presentDate.getMinutes() - postDate.getMinutes()} minutes ago`; */
      },
    },

    components: {},
  }
</script>

<style lang="css" scoped>
  .card {
    margin-bottom: 40px;
    box-shadow: 4px 4px 14px 0px #c3baba;
  }

  .card-header svg {
    margin-top: 3.2px;
    margin-bottom: -3.2px;
  }

</style>